<template>
    <div class="mx-12">
        <div class="w-full flex items-center justify-center mr-2 mb-5">
            <div
                @dragenter.prevent="dragEnter"
                @dragleave.prevent="dragLeave"
                @dragover.prevent="dragOver"
                @drop.prevent="drop"
                id="dropZone"
                class="w-full h-28 rounded-xl border-4 border-dashed border-blue-400"
                :class="{
                    'transition-all transform duration-500 bg-blue-400 text-white':
                        entered,
                    'transition-all transform duration-500 bg-transparent text-blue-400':
                        !entered,
                }">
                <div
                    class="flex w-full h-full items-center flex-col justify-center">
                    <svg
                        xmlns="http://www.w3.org/2000/svg"
                        class="h-10 w-10"
                        fill="none"
                        viewBox="0 0 24 24"
                        stroke="currentColor"
                        stroke-width="2">
                        <path
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
                    </svg>
                    <span v-if="!name" class="text-xs font-bold"
                        >{{ $t('recipesBook.addToRecipeBookButton') }}
                    </span>
                    <span v-if="name" class="text-xs font-bold"
                        >{{ name }}
                    </span>
                    <span v-if="action" class="text-xs font-bold"
                        >{{ action }}
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        book: {
            type: Object,
            default: null,
        },
    },
    data() {
        return {
            entered: false,
            droped: false,
            name: null,
            action: null,
        }
    },
    methods: {
        dragEnter(e) {
            this.clear()
        },

        dragOver(e) {
            this.entered = true
            this.name = e.dataTransfer.getData('card_name')
        },
        dragLeave(e) {
            this.entered = false
            this.name = false
        },
        drop(e) {
            const id = e.dataTransfer.getData('card_id')
            axios
                .post(`${this.$app_url}book/${this.book.id}/recipe`, {
                    id: id,
                })
                .then((res) => {
                    this.droped = true
                    this.action = this.$t('recipesBook.wasAddedToRecipeBook')
                    this.$root.$emit('addedToBook', id)
                    setTimeout(() => {
                        this.clear()
                    }, 4000)
                })
                .catch((err) => {
                    this.droped = true
                    this.action = this.$t(
                        'recipesBook.alreadyAddedToRecipeBook'
                    )
                    setTimeout(() => {
                        this.clear()
                    }, 4000)
                })
        },
        clear() {
            this.entered = false
            this.droped = false
            this.action = null
            this.name = null
        },
    },
}
</script>

<style lang="scss" scoped></style>
